.sub-slider__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 30;
  pointer-events: none;
  background: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.4),
    transparent,
    transparent,
    transparent,
    rgba(0, 0, 0, 0.4)
  );
}
